function Sparkle(props) {
    const symbol = props.star
    const x = props.x;
    const y = props.y;
    const style = {
        top: y + 'px',
        left: x + 'px',
    };
    const sparkleclass = props.sparkleclass;

    return (
        <div className={sparkleclass} style={style} alt="" >{symbol}</div>
    );
}

class Page extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            display: "index",
        };

        this.handleClick = this.handleClick.bind(this);
    }

    handleClick(id) {
        this.setState(() => ({
            display: id
        }));
    }

    render() {
        let get_pages = {
            index: {
                name : "Home",
                content: Index()
            },
            about: {
                name: "About",
                content: About()
            },
            research: {
                name: "Research",
                content: Research()
            },
            mellon: {
                name: "meLLON",
                content: Mellon()
            }
        };

        const pagetext = get_pages;
        const pages = Object.keys(pagetext);
        const navButtons = pages.map((page) => 
            <NavButton key={page} id={page} label={pagetext[page].name} onClick={this.handleClick} />
        );
        const page = pagetext[this.state.display].content;

        return (
            <div className="mainContent">
                <Header name={this.props.name["nick"]} />
                <div className="nav-buttons"> 
                    <div className="row justify-content-center">
                        {navButtons}
                    </div>
                </div>
                <PaneContent page={page} />
                <Footer />
            </div>
        );
    }
}

class SparklePage extends React.Component {
    constructor(props) {
        super(props);
        
        this.state = {
            sparkles: [],
            sparklecounter: 0,
            index: -1,
            play: false
        }

        this.handleMove = this.handleMove.bind(this);
    }

    handleMove(e) {
        var available_class = ["sparkle-left", "sparkle-right", "sparkle-center"];
        var class_index = Math.floor(Math.random() * available_class.length);

        // var available_stars = ["", " ", "", ""];
        // var available_stars = [""];
        var available_stars = ["", "", ""];

        var star_index = Math.floor(Math.random() * available_stars.length);

        const sparkleclass = available_class[class_index];
        const star = available_stars[star_index];
        const sparkleCounter = this.state.sparklecounter + 1;
        const index = (this.state.index + 1) % 100;

        var sparkles = this.state.sparkles;
        sparkles[index] =
            {
                x: e.clientX,
                y: e.clientY,
                key: sparkleCounter,
                class: sparkleclass,
                symbol: star
            };

        this.setState({
            sparkles: sparkles,
            sparklecounter: sparkleCounter,
            index: index
        });
    }

    render() {
        const name = this.props.name;
        const sparkleinfo = this.state.sparkles;

        const sparkles = sparkleinfo.map((sparkle) =>
            <Sparkle x={sparkle.x} y={sparkle.y} star={sparkle.symbol} sparkleclass={sparkle.class} key={sparkle.key} />
        );

        return (
            <div onMouseMove={this.handleMove} className="sparklePage">
                <Page name={name} />
                {sparkles}
            </div>
        )
    }
}


